}

.App-logo2 {
  float: left;
  width: 191px;
  height: 61px;
  -webkit-transition: margin .125s ease;
  -o-transition: margin .125s ease;
  transition: margin .125s ease;
  {/*border: 2px solid rgba(255,131,0);*/}
}

.Logo:hover {
  cursor: pointer;
  margin-top: 5px;
}


