.Footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 60px;
  color: #f5f5f1;
  background-color: #0b3c5d;
  border-top: 8px solid #f5f5f1;
}
.Footer > p {
  position: relative;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 5px;
  background-color: #0b3c5d;
  padding-bottom: 10px;
  padding-left: 10px;
  margin: 0;
  font-weight: 800;
  padding-top: 10px;
  text-align: center;
}

@media (max-width:767px) {
  .Footer {
    text-align:center;
    padding-bottom:20px;
  }
}

@media (max-width:991px) {
  .Footer {
    text-align:center;
  }
}




